import { de } from './de.js';
import { en } from './en.js';
import { Language } from '../settings.js';


export interface Translations {
  [category: string]: string | Translations;
}

export const languages: Record<Language, Translations> = { en, de };



export async function loader(lang: string): Promise<Translations> {
  if (Object.keys(languages).includes(lang)) {
    return languages[<Language>lang];
  } else {
     return {} as Translations;
  }
}
